import React, { Component } from 'react';
import '../App.css';
import Navbar from './Navbar';
import About from './About';
import Resume from './Resume';
import Projects from './Projects';
import Skills from './Skills';
import Footer from './Footer';
import $ from 'jquery';

class App extends Component {

	constructor(props) {
		super(props);
		this.state = { aboutIsVisible: false, resumeIsVisible: false, projectsIsVisible: false, skillsIsVisible: false }
	}

	// componentDidMount() {
	// 	// if ($('body').hasClass('mobile')) {
	// 	var viewportHeight = $('.page').outerHeight();
	// 	$('.page').css({ height: viewportHeight });
	// 	// }
	// }

	handleViewportChange = (isVisible, page) => {
		console.log(page, isVisible);
		if (page === 'about') {
			this.setState({ aboutIsVisible: isVisible });
		} else if (page === 'resume') {
			this.setState({ resumeIsVisible: isVisible });
		} else if (page === 'projects') {
			this.setState({ projectsIsVisible: isVisible });
		} else if (page === 'skills') {
			this.setState({ skillsIsVisible: isVisible });
		}
	}

	render() {
		return (
			<div className='container-fluid'>
				<header className='App-header'>
					<Navbar
						aboutIsVisible={this.state.aboutIsVisible}
						resumeIsVisible={this.state.resumeIsVisible}
						projectsIsVisible={this.state.projectsIsVisible}
						skillsIsVisible={this.state.skillsIsVisible}
					/>
					<About onViewportChange={this.handleViewportChange} />
					<Resume onViewportChange={this.handleViewportChange} />
					<Projects onViewportChange={this.handleViewportChange} />
					<Skills onViewportChange={this.handleViewportChange} />
					<Footer />
				</header>
			</div >
		);
	}
}

export default App;
