import React, { Component } from 'react';
import me from '../me.png';
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";
import VisibilitySensor from 'react-visibility-sensor';


class Skills extends Component {

    onChange = (isVisible) => {
        this.props.onViewportChange(isVisible, 'skills');
    }

    render() {
        let marginBottom = {
            marginBottom: '20px',
        }

        return (
            // <VisibilitySensor onChange={this.onChange} partialVisibility={true} minTopValue={800}>
            <ScrollAnimation animateIn='fadeIn' animateOut='fadeOut'
                afterAnimatedIn={() => { this.props.onViewportChange(true, 'skills') }}
                afterAnimatedOut={() => { this.props.onViewportChange(false, 'skills') }}
            >
                <div className="page content-page row" id="skills">
                    <img src={me} className='me' style={marginBottom} alt='logo' />
                    {/* <TweenComponent /> */}
                    <div className="header-bg">
                        <h1 className="title"> Skills </h1>
                    </div>
                    <p>
                        (also coming soon!)
                    </p>
                </div>
            </ScrollAnimation>
            // </VisibilitySensor>
        );
    }
}

export default Skills;